import { Nullable } from "@/lib/types";
import { Card, CardAnswer, AnswerUtils } from "@/lib/entity/card";

interface ReviewMetadata {
  answeredCorrectly: Nullable<boolean>; //null will be not answered
}

// Slightly annoying wrapper around card, so that I can track ephemoral card information related to the lesson.
// e.g. whether the card was answered correctly or not.
class ReviewCard {
  constructor(public card: Card, public metadata: ReviewMetadata) {}

  answer(answer: CardAnswer): void {
    this.card.answer(answer);
    this.metadata.answeredCorrectly = AnswerUtils.isCorrectAnswer(answer);
  }
}

class LessonConfig {} // Not implemented yet

/*
 * A lesson (or review session) is an ephemoral collection of cards representing what you're currently learning or reviewing.
 *
 * Its resposible for maintaining the state of what cards you're currently reviewing and if you've finished reviewing or not.
 *
 * The current implementation is a very simple review-each-card-once, but it might change to have lessons that will re-review incorrect cards
 * or lessons that can create child lessons of all incorrect cards. At that point it should be extracted out into an interface.
 */
class Lesson {
  readonly cards: Array<ReviewCard>;
  private currentIndex: number;

  constructor(
    inputCards: Array<Card>,
    public config: LessonConfig = new LessonConfig()
  ) {
    this.cards = inputCards.map(
      c =>
        new ReviewCard(c, {
          answeredCorrectly: null
        })
    );
    this.currentIndex = 0;
  }

  restart(): void {
    this.currentIndex = 0;
  }

  moveToNextCard(): Nullable<ReviewCard> {
    //advances the lesson to the next card. Also returns that card (which will be null if there is no next card), but you can ignore it
    this.currentIndex++;
    return this.getCurrentReviewCard();
  }

  getCurrentReviewCard(): Nullable<ReviewCard> {
    if (!this.isReviewComplete()) {
      return this.cards[this.currentIndex];
    }
    return null;
  }

  isReviewComplete(): boolean {
    return this.currentIndex >= this.cards.length;
  }
}

export { ReviewCard, Lesson };
