import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "@/vue/views/Home.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/card/front",
    name: "Flashcard-front",
    component: () =>
      import(/* webpackChunkName: "card" */ "../views/CardFront.vue")
  },
  {
    path: "/card/back",
    name: "Flashcard-back",
    component: () =>
      import(/* webpackChunkName: "card" */ "../views/CardBack.vue")
  },
  {
    path: "/lesson",
    name: "Lesson",
    component: () =>
      import(/* webpackChunkName: "lesson" */ "../views/Lesson.vue")
  },
  {
    path: "/lesson/review",
    name: "LessonReview",
    component: () =>
      import(/* webpackChunkName: "lesson" */ "../views/LessonReview.vue")
  },
  {
    path: "/decks",
    name: "Decks",
    component: () =>
      import(/* webpackChunkName: "decks" */ "../views/Decks.vue")
  },
  {
    path: "/inspect/decks",
    name: "Inspect Decks",
    component: () =>
      import(
        /* webpackChunkName: "inspect" */ "../views/inspect/InspectDecks.vue"
      )
  },
  {
    path: "/inspect/decks/:id",
    name: "Inspect Deck",
    component: () =>
      import(
        /* webpackChunkName: "inspect" */ "../views/inspect/InspectDeck.vue"
      )
  },
  {
    path: "/inspect/cards/:id",
    name: "Inspect Card",
    component: () =>
      import(
        /* webpackChunkName: "inspect" */ "../views/inspect/InspectCard.vue"
      )
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
