import { Note } from "@/lib/entity/note";
import { Template } from "@/lib/entity/template";

const SRS_MULTIPLIER_MS = 60 * 60 * 1000; // set to 1 hour

// helper function to get time as epoch millis
function now(): number {
  return new Date().getTime();
}

// Enum for the status of a card (whether its new or already learned)
enum CardStatus {
  NEW = "NEW",
  LEARNED = "LEARNED"
}

/*
  Enum for representing how easy/hard it was to remember a word,
  and the SRS multiplication factor for when the card should be reviewed next
*/
enum CardAnswer {
  // Wrong answer
  WRONG = "WRONG",

  //Correct answers
  EASY = "EASY",
  MEDIUM = "MEDIUM",
  HARD = "HARD"
}

interface CorrectAnswerValues {
  EASY: number;
  MEDIUM: number;
  HARD: number;
}

const AnswerUtils = {
  isCorrectAnswer: function(answer: CardAnswer): boolean {
    return (
      answer == CardAnswer.EASY ||
      answer == CardAnswer.MEDIUM ||
      answer == CardAnswer.HARD
    );
  }
};

const DEFAULT_CORRECT_ANSWER_VALUES: CorrectAnswerValues = {
  EASY: 2.5,
  MEDIUM: 2,
  HARD: 1.5
};

class AuditEvent {
  constructor(readonly date: number, readonly answer: CardAnswer) {}
}

/*
 * A card represents a question/answer pair, and will determine (automatically) if it should be reviewed again.
 * This can be thought of as a flashcard (with a front and back), with additional data on whether you need to review it again.
 *
 * The card is made up of: a note, which stores the base information about the card; and a template
 * which has logic for how to render the card.
 *
 * The cards main responsibilities are: determine how to answer the card, and determine when to review the
 * card next.
 *
 * Note: this may need to be extracted to an interface if I want to try out different SRS algorithms.
 */
class Card {
  readonly id: string;
  readonly auditEvents: Array<AuditEvent>;
  private nextReviewTimeMs: number;
  private nextReviewTimeCalculationMetadata = {
    correctInARow: 0
  };

  constructor(public note: Note, public template: Template) {
    this.id = `${note.id}_${template.id}`;

    this.auditEvents = [];
    this.nextReviewTimeMs = now();
  }

  answer(answer: CardAnswer): void {
    this.auditEvents.push({
      date: now(),
      answer
    });
    this.calculateNextReviewTime(answer);
  }

  isReadyToReview(): boolean {
    const currentTime = now();
    return this.nextReviewTimeMs <= currentTime;
  }

  compare(other: Card): number {
    return this.nextReviewTimeMs - other.nextReviewTimeMs;
  }

  private calculateNextReviewTime(answer: CardAnswer): void {
    if (answer === CardAnswer.WRONG) {
      this.nextReviewTimeMs = now();
      this.nextReviewTimeCalculationMetadata.correctInARow = 0;
    } else {
      this.nextReviewTimeCalculationMetadata.correctInARow += 1;
      const nextReviewTimeMs =
        DEFAULT_CORRECT_ANSWER_VALUES[answer] **
          this.nextReviewTimeCalculationMetadata.correctInARow *
        SRS_MULTIPLIER_MS;
      this.nextReviewTimeMs = nextReviewTimeMs + now();
    }
  }

  getStatus(): CardStatus {
    if (
      this.auditEvents.filter(e => AnswerUtils.isCorrectAnswer(e.answer))
        .length > 0
    ) {
      return CardStatus.LEARNED;
    }
    return CardStatus.NEW;
  }
}

export { Card, CardStatus, CardAnswer, AnswerUtils };
